import React, { useState, useEffect } from 'react'
import Card from './components/Card'


const App = () => {

    const [sueldo, setSueldo] = useState();
    const [subsidioTransporte, setSubsidioTransporte] = useState(0);

    const handleChange = (event) => {
        setSueldo(event.target.value);
    }

    useEffect(()=>{

        sueldo >= 5000 ? 
        setSubsidioTransporte(100) : 
        setSubsidioTransporte(0)
      

    }, [sueldo])

    return (
        <div className="container mx-auto w-3/5 border mb-5 rounded">
            <h1 className="text-2xl p-5 text-center border text-gray-900 bg-gray-200 font-bold">CALCULO PRIMAS SEMESTRALES SEGUNDO SEMESTRE 2021</h1>
            <div className="flex flex-wrap">
                <form className="w-full sm:w-1/2 text-center p-5">
                    <label className="text-lg font-bold pt-5 m-2 text-center text-gray-600" for="caja_1">Sueldo básico a Noviembre 30 (*)</label>
                    <br></br>
                    <input onChange={handleChange} value={sueldo} className="border shadow m-2 w-full rounded leading-tight focus:outline-none focus:shadow-outline" type="number" name="caja_1" id="caja_1" placeholder="0"></input>
                    <p class="text-red-500 text-xs italic">Este espacio no debe permanecer vacío.</p>
                    <br></br>

                    <h1 className="font-bold text-2xl pt-5 text-gray-600">VALORES PROPORCIONALES</h1>
                    <br></br>

                    <label className="text-lg font-bold pt-5 m-2 text-center text-gray-600" for="caja_2">Suma de lo pagado por horas extras en el semestre</label>
                    <br></br>
                    <input className="shadow border m-2 w-full rounded leading-tight focus:outline-none focus:shadow-outline" type="number" name="caja_2" id="caja_2" placeholder="0"></input>
                    <p class="text-red-500 text-xs italic">Este espacio no debe permanecer vacío.</p>
                    <br></br>

                    <label className="text-lg font-bold pt-5 m-2 text-center text-gray-600" for="caja_3">Valor pagado por prima de vacaciones en el semestre
                    (JULIO 1 A DICIEMBRE 31)</label>
                    <br></br>
                    <input className="shadow border m-2 w-full rounded leading-tight focus:outline-none focus:shadow-outline" type="number" name="caja_3" id="caja_3" placeholder="0"></input>
                    <p class="text-red-500 text-xs italic">Este espacio no debe permanecer vacío.</p>
                </form>
                <div className="w-full sm:w-1/2 pb-5 px-3">
                    <h1 className="text-lg font-bold pt-2 m-2 text-center text-gray-600">Subsidio de almuerzo con efecto prestacional</h1>
                    <h1 className="text-2xl font-bold text-center">$0</h1>
                    
                    <h1 className="text-lg font-bold pt-5 m-2 text-center text-gray-600">Subsidio de transporte</h1>
                    <h1 className="text-2xl font-bold text-center">${subsidioTransporte}</h1>
                    
                    <h1 className="text-lg font-bold pt-5 m-2 text-center text-gray-600">Horas extras en el semestre</h1>
                    <h1 className="text-2xl font-bold text-center">$0</h1>
                    
                    <h1 className="text-lg font-bold pt-5 m-2 text-center text-gray-600">Prima de vacaciones pagadas en el semestre</h1>
                    <h1 className="text-2xl font-bold text-center">$0</h1>
                    
                    <h1 className="text-lg font-bold pt-5 m-2 text-center text-gray-600">TOTAL PRIMA SEMESTRAL</h1>
                    <h1 className="text-2xl font-bold text-center">$0</h1>
                    
                    <h1 className="text-lg font-bold pt-5 m-2 text-center text-gray-600">TOTAL PRIMA SERVICIOS</h1>
                    <h1 className="text-2xl font-bold text-center">$0</h1>
                    
                    <h1 className="text-lg font-bold pt-5 m-2 text-center text-gray-600">BONIFICACION DE NAVIDAD</h1>
                    <h1 className="text-2xl font-bold text-center">$0</h1>
                    
                    <h1 className="text-lg font-bold pt-5 m-2 text-center text-gray-600">VALOR TOTAL POR CONCEPTO DE PRIMAS</h1>
                    <h1 className="text-2xl font-bold text-center">$0</h1>
                </div>
            </div>
        </div>
    )
}

export default App